.navbar{
    background-color: rgb(21, 20, 20);
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    /* border: 2px solid red; */
    height: 7%;
   }
.menubar{

    font-size: large;
    margin: 10px;
    font-weight: 500;
    color: rgb(0, 0, 0);
    text-decoration: none;
}
.btn{
    border: none;
    background-color:rgb(183, 183, 183);
    color: black;
    font-size: medium;
    padding: 5px;
    border-radius: 10px;
    cursor: pointer;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.dark{
    color: white;
    background-color: black;
}
.light{
    color: black;
    background-color: white;
}
.active{
    color:rgb(171, 223, 0)
}